
.backToTop {
    position: relative;
}
.backToTop div {
    height: 50px;
}
.bthTop {
    position: fixed;
    bottom: 40px;
    right: 40px;
    width: 80px;
    height: 40px;
    line-height: 40px;
    border: 1px solid red;
    text-align: center;
}
* {
	margin: 0;
	padding: 0;
}

ul, li {
	list-style: none;
}

input {
	border: none;
}

img {
	max-width: 100%;
}

#app {
	width: 100%;
	height: 100%;
}

.container {
	max-width: 100%;
}

.sidebar {
	position: fixed;
	top: 0;
	left: 0;

	width: 210px;
	height: 100%;

	color: #fff;
	background-color: #304156;
	overflow-y: auto;
}

.sidebar::-webkit-scrollbar {
    display: none;
}

.sidebar_lia {
	position: relative;
	pointer-events: auto;
}

.sidebar_lia_hide > ul {
	display: none;
}

.sidebar_lia_show > ul {
	display: block;
}

.sidbar_title {
	padding: 0 20px;

	height: 54px;

	line-height: 54px;

	pointer-events: auto;
}

.sidbar_title:hover {
	background-color: #263445;
}

.sidbar_title .ioc_1 {
	display: inline-block;

	margin-right: 8px;

	width: 16px;
	height: 16px;

	background-color: #888;
}

.arrow {
	position: absolute;
	top: 20px;
	right: 30px;

	width: 10px;
	height: 10px;

	border-top: 1px solid red;
	border-right: 1px solid red;

	-webkit-transform: rotateZ(45deg);

	        transform: rotateZ(45deg);
}

.sidebar_secondary {
	max-width: 100%;
	height: 50px;

	line-height: 50px;

	background-color: #1f2d3d;
}

.sidebar_secondary:hover {
	background-color: #001528;
}

#content {
	margin-left: 210px;

	max-width:100%;
	height: 2000px;
}

.com_header {
	display: -ms-flexbox;
	display: flex;
	display: -webkit-box;
}

.com_header p {
	-webkit-box-flex: 0,;
	    -ms-flex: 0, 0, 30%;
	        flex: 0, 0, 30%;
	width: 30%;
	text-align: center;
}

.register {
	margin: 100px auto;
	max-width:400px; 
}

.register_title {
	text-align: center;
}

.register_input {
	margin: 0 auto;
	width: 200px;
}

.register_input input {
	margin: 5px 0;
	width: 100%;
	height: 24px;
	line-height: 24px;
	background-color: #e4f30c;
	color: #f30c6c;
}

.register_input div {
	height: 28px;
	line-height: 28px;
	font-weight: bold;
	text-align: center;
}

.is_active {
	color: red;
}

.project {
	width: 100%;
}

.project_header {
	width: 100%;
	height: 50px;
	background-color: #000;
}

.project_load {
	float: left;
	margin-left: 30px;
	line-height: 50px;
	color: #fff;
}

.project_list {
	float: left;
	width: 50%;
	color: #fff;
	line-height: 50px;
	font-size: 24px;
}

.project_list li {
	float: left;
	margin-left: 30px;
	min-width: 100px;
	text-align: center;
}

.project_personal {
	float: right;
	margin-right: 40px;
	line-height: 50px;
}

.project_personal_img {
	height: 50px;
}

.project_personal_img img {
	float: left;
	margin-top: 5px;
	margin-right: 20px;
	width: 40px;
}

.project_personal_img span {
	color: #fff;
}

.project_personal_list {
	display: none;
	font-size: 20px;
	text-align: center;
}

.project_personal_list li {
	height: 50px;
}